	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Everything is changing. Everything is new. Frameworks, platforms and trends are displaced on a weekly basis. Skills are churning.\n\nAnd yet... Beneath this seemingly turbulent flow there is a slow current, strong and steady, changing relatively little over the decades. Concepts with a long history appear in new forms and fads and technologies. Principles are revisited. Ideas once lost to the mainstream are found again.\n\nIn this keynote we revisit the present through the past, looking at the enduring principles that shape programming languages, architecture, development practice and development process, the ideas that cycle round, each time becoming perhaps a little better defined, a little more mature, and look to see what else might be on the horizon.","meta":{"duration":"PT50M23S"},"presenter":{"name":"Kevlin Henney"},"source":{"id":"AbgsfeGvg3E","provider":"youtube"},"slug":"old-is-the-new-new","tag":["GOTO Conference","Keynote","Architecture","Languages"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/AbgsfeGvg3E/default.jpg","height":90,"width":120}},"title":"Old Is the New New"}}]}},"pathContext":{"slug":"languages","tag":"Languages"}}

